import { useState } from 'react'
import { Helmet } from 'react-helmet'
import { useParams } from 'react-router-dom'
import PropTypes from 'prop-types'

import Loading from '../Templates/Loading'

const Product = ({ admin }) =>
{
    const [product, setProduct] = useState(null)
    const [id] = useState(useParams().id)
    const [loaded, setLoaded] = useState(false)

    // Component did mount
    const URL = `http://localhost:3200/api/products/${id}`
    const XHR = new XMLHttpRequest()

    const loadingStyle = { justifyContent: loaded ? "initial" : "center" }
    
    
    if (!loaded)
    {
        XHR.open("GET", URL)

        XHR.onreadystatechange = () =>
        {
            if (XHR.readyState === 4)
            {
                if (XHR.status === 200)
                {
                    const response = JSON.parse(XHR.responseText)
                    setProduct(response.product)
                }

                setLoaded(true)
            }
        }

        XHR.send()
    }

    // Render
    if (product)
        var { _id, name, price, description, quantity, imageUrl } = product;

    return [
        loaded ? (
            product ? (
                <h1 className="title" key={0}>{ name }</h1>
            ) : (
                <h1 className="title" key={1}>Produit inexistant</h1>
            )
        ) : (
            <h1 className="title" key={2}>Encore un peu de patience...</h1>
        ),

        <div className="product" style={ loadingStyle } key={3}>
        {
            loaded ? (
                product ? [
                    <Helmet key={4}>
                        <title>{ name } - STUFF MANAGER</title>
                    </Helmet>,
                    <img alt={ name } src={ imageUrl } className="product__img" key={5}/>,
                    <div className="product__aside" key={6}>
                        <p className="description">{ description }</p>

                        <div className="otherInfos">
                            <p>
                                Prix : <span className="price">{ price }</span>
                            </p>
                            <p>
                                Quantité : <span>{ quantity > 0 ? quantity : "Aucune."}</span>
                            </p>
                            {
                                admin && [
                                    <p key={6}>
                                        <a className="link" href={ `/product/${_id}/modify` } title="Modifier">Modifier</a>
                                    </p>,
                                    <p key={7}>
                                        <a className="link" href={ `/product/${_id}/delete` } title="Supprimer">Supprimer</a>
                                    </p>
                                ]
                            }
                        </div>
                    </div>
                ] : (
                    <p className="error">Ce produit n'existe pas ou a été retiré.</p>
                )
            ) : <Loading/>
        }
        </div>
    ]
}

Product.propTypes = {
    admin: PropTypes.bool.isRequired
}

export default Product