import { useState } from 'react'
import { Helmet } from 'react-helmet'
import { useParams } from 'react-router-dom'
import PropTypes from 'prop-types'

import E403 from './E403'
import Loading from '../Templates/Loading'

const ModifyProductForm = ({ token }) =>
{
    const [description, setDescription] = useState("")
    const [error, setError] = useState(null)
    const [id] = useState(useParams().id)
    const [imageUrl, setImageUrl] = useState("")
    const [fileChecked, setFileChecked] = useState(false)
    const [linkChecked, setLinkChecked] = useState(false)
    const [loaded, setLoaded] = useState(false)
    const [name, setName] = useState("")
    const [price, setPrice] = useState("")
    const [product, setProduct] = useState(null)
    const [quantity, setQuantity] = useState(null)
    const [success, setSuccess] = useState(null)

    function isNotEmpty(str)
    {
        return str.trim() !== ""
    }

    function onChangeDescription(event)
    {
        setDescription(event.target.value)
    }

    function onChangeName(event)
    {
        let val = event.target.value
        setName(val)
        event.target.style.borderBottomColor = isNotEmpty(val) ? "green" : "red"
    }

    function onChangePrice(event)
    {
        let val = event.target.value
        let isValid = str => str.match(/^[0-9]+\.?([0-9]{1,2})?$/)

        if (isValid(val) || val === "")
            setPrice(val)
        
        event.target.style.borderBottomColor = isValid(price) ? "green" : "red"
    }

    function onChangeQuantity(event)
    {
        let val = event.target.value
        let isValid = str => str.match(/^[0-9]+$/)

        if (isValid(val) || val === "") 
            setQuantity(val)
        
        event.target.style.borderBottomColor = isValid(quantity) ? "green" : "red"
    }

    function onChangeImageUrl(event)
    {
        let val = event.target.value
        setImageUrl(val)
        event.target.style.borderBottomColor = isNotEmpty(val) ? "green" : "red"
    }

    function onSelectRadioLink()
    {
        setLinkChecked(true)
        setFileChecked(false)
    }

    function onSelectRadioFile()
    {
        setLinkChecked(false)
        setFileChecked(true)
    }

    function onSubmit(event)
    {
        event.preventDefault()
        
        const URL = `http://localhost:3200/api/products/${id}`
        const XHR = new XMLHttpRequest()

        XHR.open("PUT", URL)
        
        XHR.onreadystatechange = () =>
        {
            if (XHR.readyState === 4)
            {
                let message = JSON.parse(XHR.responseText).message
                setError(XHR.status === 201 ? null : message)
                setSuccess(XHR.status === 201 ? message : null)
            }
        }

        XHR.setRequestHeader('Content-type', 'application/json')
        XHR.setRequestHeader('Authorization', token)
        XHR.send(JSON.stringify({
            name: name,
            price: Number.parseFloat(price),
            description: description,
            quantity: Number.parseInt(quantity),
            imageUrl: imageUrl
        }))
    }

    // Component did mount
    if (!loaded)
    {
        const URL = `http://localhost:3200/api/products/${id}`
        const XHR = new XMLHttpRequest()

        XHR.open("GET", URL)

        XHR.onreadystatechange = () =>
        {
            if (XHR.readyState === 4)
            {
                let response = JSON.parse(XHR.responseText)

                if (XHR.status === 200)
                {
                    let { name, description, price, imageUrl, quantity } = response.product
                    
                    setProduct(response.product)
                    setName(name)
                    setDescription(description ? description : "")
                    setPrice(`${price}`)
                    setQuantity(`${quantity}`)
                    setImageUrl(imageUrl)
                }
                
                setLoaded(true)
            }
        }

        XHR.send()
    }

    // Render
    const loadingStyle = { display: "flex", justifyContent: "center" }
    
    if (!token)
        return <E403/>

    if (!loaded)
        return [
            <h1 className="title" key={0}>Encore un peu de patience...</h1>,
            <div style={ loadingStyle } key={1}><Loading/></div>,
        ]

    if (!product)
        return [
            <h1 className="title" key={0}>Produit inexistant</h1>,
            <p className="error" key={1}>Ce produit n'existe pas ou a été retiré.</p>,
        ]

    const submit = isNotEmpty(name) && isNotEmpty(price)
        && isNotEmpty(quantity) && isNotEmpty(imageUrl)

    return [
        <Helmet key={0}>
            <title>Modifier un produit - STUFF MANAGER</title>
        </Helmet>,
        <h1 className="title" key={1}>Modifier le produit</h1>,
        error && <p className="error" key={2}>{ error }</p>,
        success && <p className="success" key={3}>{ success }</p>,
        <form action="" method="POST" onSubmit={ onSubmit } key={4}>
            <div className="field">
                <label htmlFor="Nom">Nom du produit</label>
                <input
                    className="textfield"
                    name="name"
                    onChange={ onChangeName }
                    placeholder="Nom du produit"
                    type="text"
                    value={ name }
                />
            </div>
            <div className="field">
                <label htmlFor="Prix">Prix (€)</label>
                <input
                    className="textfield"
                    inputMode="numeric"
                    name="price"
                    onChange={ onChangePrice }
                    placeholder="12.34"
                    type="text"
                    value={ price }
                />
            </div>
            <div className="field">
                <label htmlFor="Description">Description</label>
                <textarea
                    className="textfield textfield--area"
                    defaultValue={ description }
                    name="description"
                    onChange={ onChangeDescription }
                    placeholder="Ici, la description..."
                >
                </textarea>
            </div>
            <div className="field">
                <label htmlFor="Quantité">Quantité</label>
                <input
                    className="textfield"
                    inputMode="decimal"
                    name="quantity"
                    onChange={ onChangeQuantity }
                    placeholder="123"
                    type="text"
                    value={ quantity }
                />
            </div>
            <div className="field">
                <label htmlFor="Quantité">Image</label>
                <div className="field__radio">
                    <input
                        checked={ linkChecked }
                        type="radio"
                        name="imageUrlOption"
                        value="link"
                        onChange={ onSelectRadioLink }
                    />
                    <input
                        className={ `textfield ${linkChecked || "textfield--disabled"}` }
                        disabled={ !linkChecked }
                        type="text"
                        onChange={ onChangeImageUrl }
                        name="imageUrl"
                        placeholder="https://cdn.urldelimage.com/image.png"
                        value={ imageUrl }
                    />
                </div>
                <div className="field__radio">
                    <input
                        checked={ fileChecked }
                        type="radio"
                        name="imageUrlOption"
                        value="file"
                        onChange={ onSelectRadioFile }
                    />
                    <input
                        className={ `textfield ${fileChecked || "textfield--disabled"}` }
                        disabled={ !fileChecked }
                        type="file"
                        name="imageUrl"
                        placeholder="Chargez votre image ici..."
                    />
                </div>
            </div>
            <div className="field">
                <input
                    className={ `button ${submit || "button--disabled"}` }
                    disabled={ !submit }
                    type="submit"
                    value="Modifier le produit"
                />
            </div>
        </form>
    ]
}

ModifyProductForm.propTypes = {
    token: PropTypes.string
}

export default ModifyProductForm