import { Component } from 'react'
import { Helmet } from 'react-helmet'
import PropTypes from 'prop-types'
import E403 from './E403'

class AddProductForm extends Component
{
    static propTypes = {
        token: PropTypes.string
    }

    state = {
        description: "",
        error: null,
        imageUrl: "",
        fileChecked: false,
        linkChecked: false,
        name: "",
        price: "",
        quantity: "",
        success: null,
    }

    isNotEmpty = str => str.trim() !== ""

    onChangeDescription = event =>
    {
        this.setState({ description: event.target.value })
    }

    onChangeName = event =>
    {
        let val = event.target.value
        this.setState({ name: val })
        event.target.style.borderBottomColor = this.isNotEmpty(val) ? "green" : "red"
    }

    onChangePrice = event =>
    {
        let val = event.target.value
        let isValid = str => str.match(/^[0-9]+\.?([0-9]{1,2})?$/)

        if (isValid(val) || val === "") 
            this.setState({ price: val })
        
        event.target.style.borderBottomColor = isValid(this.state.price) ? "green" : "red"
    }

    onChangeQuantity = event =>
    {
        let val = event.target.value
        let isValid = str => str.match(/^[0-9]+$/)

        if (isValid(val) || val === "") 
            this.setState({ quantity: val })
        
        event.target.style.borderBottomColor = isValid(this.state.quantity) ? "green" : "red"
    }

    onChangeImageUrl = event =>
    {
        let val = event.target.value
        this.setState({ imageUrl: val })
        event.target.style.borderBottomColor = this.isNotEmpty(val) ? "green" : "red"
    }

    onSelectRadioLink = () =>
    {
        this.setState({
            linkChecked: true,
            fileChecked: false
        })
    }

    onSelectRadioFile = () =>
    {
        this.setState({
            linkChecked: false,
            fileChecked: true
        })
    }

    onSubmit = event =>
    {
        event.preventDefault();

        const { name, price, description, quantity, imageUrl } = this.state
        const URL = "http://localhost:3200/api/products"
        const XHR = new XMLHttpRequest()

        XHR.open("POST", URL)
        
        XHR.onreadystatechange = () =>
        {
            if (XHR.readyState === 4)
            {
                let response = JSON.parse(XHR.responseText)

                if (XHR.status === 201)
                    this.setState({ error: null, success: response.message })
                else
                    this.setState({ error: response.message, success: null })
            }
        }

        XHR.setRequestHeader('Content-type', 'application/json')
        XHR.setRequestHeader('Authorization', this.props.token)
        XHR.send(JSON.stringify({
            name: name,
            price: Number.parseFloat(price),
            description: description,
            quantity: Number.parseInt(quantity),
            imageUrl: imageUrl
        }))
    }

    render()
    {
        const { error, fileChecked, linkChecked, success } = this.state
        const { name, price, quantity, imageUrl } = this.state
        const { token } = this.props
        
        const submit = this.isNotEmpty(name) && this.isNotEmpty(price)
            && this.isNotEmpty(quantity) && this.isNotEmpty(imageUrl)

        if (!token)
            return <E403/>

        return [
            <Helmet key={0}>
                <title>Ajouter un produit - STUFF MANAGER</title>
            </Helmet>,
            <h1 className="title" key={1}>Ajouter un produit</h1>,
            error && <p className="error" key={2}>{ error }</p>,
            success && <p className="success" key={3}>{ success }</p>,
            <form action="" method="POST" onSubmit={ this.onSubmit } key={4}>
                <div className="field">
                    <label htmlFor="Nom">Nom du produit</label>
                    <input
                        className="textfield"
                        name="name"
                        onChange={ this.onChangeName }
                        placeholder="Nom du produit"
                        type="text"
                    />
                </div>
                <div className="field">
                    <label htmlFor="Prix">Prix (€)</label>
                    <input
                        className="textfield"
                        inputMode="numeric"
                        name="price"
                        onChange={ this.onChangePrice }
                        placeholder="12.34"
                        type="text"
                        value={ price }
                    />
                </div>
                <div className="field">
                    <label htmlFor="Description">Description</label>
                    <textarea
                        className="textfield textfield--area"
                        name="description"
                        onChange={ this.onChangeDescription }
                        placeholder="Ici, la description..."
                    >
                    </textarea>
                </div>
                <div className="field">
                    <label htmlFor="Quantité">Quantité</label>
                    <input
                        className="textfield"
                        inputMode="decimal"
                        name="quantity"
                        onChange={ this.onChangeQuantity }
                        placeholder="123"
                        type="text"
                        value={ quantity }
                    />
                </div>
                <div className="field">
                    <label htmlFor="Quantité">Image</label>
                    <div className="field__radio">
                        <input
                            checked={ linkChecked }
                            type="radio"
                            name="imageUrlOption"
                            value="link"
                            onChange={ this.onSelectRadioLink }
                        />
                        <input
                            className={ `textfield ${linkChecked || "textfield--disabled"}` }
                            disabled={ !linkChecked }
                            type="text"
                            onChange={ this.onChangeImageUrl }
                            name="imageUrl"
                            placeholder="https://cdn.urldelimage.com/image.png"
                        />
                    </div>
                    <div className="field__radio">
                        <input
                            checked={ fileChecked }
                            type="radio"
                            name="imageUrlOption"
                            value="file"
                            onChange={ this.onSelectRadioFile }
                        />
                        <input
                            className={ `textfield ${fileChecked || "textfield--disabled"}` }
                            disabled={ !fileChecked }
                            type="file"
                            name="imageUrl"
                            placeholder="Chargez votre image ici..."
                        />
                    </div>
                </div>
                <div className="field">
                    <input
                        className={ `button ${submit || "button--disabled"}` }
                        disabled={ !submit }
                        type="submit"
                        value="Ajouter le produit"
                    />
                </div>
            </form>
        ]
    }
}

export default AddProductForm