import { Component } from 'react'
import { Helmet } from 'react-helmet'

class RegisterForm extends Component
{
    state = {
        error: null,
        success: null,
        email: "",
        login: "",
        password: ""
    }

    isValidEmail = () =>
    {
        let validEmail = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
        return this.state.email.match(validEmail)
    }

    isValidLogin = () =>
    {
        let validLogin = /^[a-zA-Z0-9]{6,}$/
        return this.state.login.match(validLogin)
    }

    isValidPassword = () =>
    {
        return this.state.password.length >= 6
    }

    onChangeEmail = event =>
    {
        this.setState({ email: event.target.value })
        event.target.style.borderBottomColor = this.isValidEmail() ? "green" : "red"
    }

    onChangeLogin = event =>
    {
        this.setState({ login: event.target.value })
        event.target.style.borderBottomColor = this.isValidLogin() ? "green" : "red"
    }

    onChangePassword = event =>
    {
        this.setState({ password: event.target.value })
        event.target.style.borderBottomColor = this.isValidPassword() ? "green" : "red"
    }

    onSubmit = event =>
    {
        event.preventDefault();
        
        const { email, login, password } = this.state
        const XHR = new XMLHttpRequest()
        const URL = "http://localhost:3200/api/auth/register"

        XHR.open("POST", URL)

        XHR.onreadystatechange = () =>
        {
            if (XHR.readyState === 4)
            {
                let response = JSON.parse(XHR.responseText)

                if (XHR.status === 201)
                    this.setState({ error: null, success: response.message })
                else
                    this.setState({ error: response.message, success: null })
            }
        }

        XHR.setRequestHeader('Content-type', 'application/json')
        XHR.send(JSON.stringify({
            email: email,
            login: login,
            password: password
        }))
    }

    render()
    {
        const { error, success } = this.state
        const submit = this.isValidEmail() && this.isValidLogin() && this.isValidPassword()

        return [
            <Helmet key={0}>
                <title>Inscription - STUFF MANAGER</title>
            </Helmet>,
            <h1 className="title" key={1}>Inscription</h1>,
            error && <p className="error" key={2}>{ error }</p>,
            success && <p className="success" key={3}>{ success }</p>,
            <form key={4} action="/" className="registerForm" method="POST" onSubmit={ this.onSubmit }>
                <div className="registerForm__field field">
                    <label htmlFor="Email">Email</label>
                    <input
                        className="textfield"
                        name="email"
                        onChange={ this.onChangeEmail }
                        placeholder="jdupont@gmail.com"
                        type="text"
                    />
                </div>
                <div className="registerForm__field field">
                    <label htmlFor="Pseudo">Pseudo</label>
                    <input
                        className="textfield"
                        type="text"
                        name="login"
                        onChange={ this.onChangeLogin }
                        placeholder="Minimum 6 caractères, alphanumériques uniquement"
                    />
                </div>
                <div className="registerForm__field field">
                    <label htmlFor="Mot de passe">Mot de passe</label>
                    <input
                        className="textfield"
                        type="password"
                        name="mdp"
                        onChange={ this.onChangePassword }
                        placeholder="Minimum 6 caractères"
                    />
                </div>
                <div className="registerForm__field field">
                    <input
                        className={`button ${submit || "button--disabled"}`}
                        disabled={ !submit }
                        type="submit"
                        value="S'inscrire"
                    />
                </div>
            </form>
        ]
    }
}

export default RegisterForm