import { Component } from 'react'
import { Helmet } from 'react-helmet'
import { Redirect } from 'react-router-dom'
import PropTypes from 'prop-types'

class LoginForm extends Component
{
    static propTypes = {
        setTokenHandle: PropTypes.func.isRequired
    }

    state = {
        login: "",
        password: "",
        redirect: null,
        error: null
    }

    onChangeLogin = (event) =>
    {
        this.setState({ login: event.target.value })
    }

    onChangePassword = (event) =>
    {
        this.setState({ password: event.target.value })
    }

    onSubmit = (event) =>
    {
        event.preventDefault()
        
        const { login, password } = this.state
        const URL = "http://localhost:3200/api/auth/login"

        var XHR = new XMLHttpRequest()
        XHR.open("POST", URL)

        XHR.onreadystatechange = () =>
        {
            if (XHR.readyState === 4)
            {
                let response = JSON.parse(XHR.responseText)
                
                if (XHR.status === 200)
                {
                    let token = response.token
                    let userId = response.userId
                    
                    this.props.setTokenHandle(token, userId)
                    this.setState({ redirect: "/", error: null })
                }
                else
                {
                    this.setState({ error: response.message })
                }
            }
        }

        XHR.setRequestHeader('Content-Type', 'application/json');
        XHR.send(JSON.stringify({
            login: login,
            email: login,
            password: password
        }))
    }

    render()
    {
        const { login, password, redirect, error } = this.state
        const submit = login.length > 0 && password.length > 0

        if (redirect)
            return (<Redirect to={ redirect }/>)

        return [
            <Helmet key={0}>
                <title>Connexion - STUFF MANAGER</title>
            </Helmet>,
            <h1 className="title" key={1}>Connexion</h1>,
            error && <p className="error" key={2}>{ error }</p>,
            <form action="/" className="loginForm" method="POST" onSubmit={ this.onSubmit } key={3}>
                <div className="loginForm__field field">
                    <label htmlFor="Email ou pseudo">Email ou pseudo</label>
                    <input className="textfield" type="text" name="login" onChange={ this.onChangeLogin } placeholder="jdupont"/>
                </div>
                <div className="loginForm__field field">
                    <label htmlFor="Mot de passe">Mot de passe</label>
                    <input className="textfield" type="password" name="mdp" onChange={ this.onChangePassword }/>
                </div>
                <div className="loginForm__field field">
                    <input className={`button ${submit || "button--disabled"}`} type="submit" value="Se connecter" disabled={ !submit }/>
                </div>
            </form>
        ]
    }
}

export default LoginForm