import { Component } from 'react'
import PropTypes from 'prop-types'

import Loading from '../Templates/Loading'

const ProductItem = ({ ID, name, price, imageUrl, firstOfRange }) =>
{
    return (
        <div className={ `productItem ${firstOfRange && "productItem--firstOfRange"}` }>
            <a href={ `/product/${ID}`} title={ name }>
                <img className="productItem__img" alt={ name } src={ imageUrl }/>
                
                <div className="productItem__caption">
                    <p className="name">{ name }</p>
                    <p className="price">{ price }</p>
                </div>
            </a>
        </div>
    )
}

ProductItem.propTypes = {
    name: PropTypes.string.isRequired,
    price: PropTypes.number.isRequired,
    imageUrl: PropTypes.string.isRequired,
    ID: PropTypes.string.isRequired,
    firstOfRange: PropTypes.bool
}

class ProductsList extends Component
{
    state = {
        products: null,
        loaded: false
    }

    componentDidMount()
    {
        const XHR = new XMLHttpRequest()

        XHR.open("GET", 'http://localhost:3200/api/products')

        XHR.onreadystatechange = () =>
        {
            if (XHR.readyState === 4)
            {
                if (XHR.status === 200)
                {
                    const response = JSON.parse(XHR.responseText)
                    this.setState({ products: response.products })
                }

                this.setState({ loaded: true })
            }
        }

        XHR.send()
    }

    render()
    {
        const { products, loaded } = this.state

        return [
            <h1 className="title" key={0}>Liste des produits</h1>,

            <div className="productsList" key={1}>
            {
                loaded ? (
                    products ? (
                        products.map((product, index) =>
                            <ProductItem
                                ID={ product._id }
                                name={ product.name }
                                price={ product.price }
                                imageUrl={ product.imageUrl }
                                key={ product._id }
                                firstOfRange={ index % 4 === 0 }
                            />)
                    ) : (
                        <p>Aucun produit pour l'instant.</p>
                    )
                ) : (
                    <Loading/>
                )
            }
            </div>
        ]
    }
}

export default ProductsList