import { useState } from 'react'
import { Helmet } from 'react-helmet'
import { PropTypes } from 'prop-types'
import { Redirect, useParams } from 'react-router-dom'

import E403 from './E403'
import Loading from '../Templates/Loading'

const DeleteProductForm = ({ token }) =>
{
    const [error, setError] = useState(null)
    const [id] = useState(useParams().id)
    const [loaded, setLoaded] = useState(false)
    const [product, setProduct] = useState(null)
    const [redirect, setRedirect] = useState(null)
    
    function onConfirm(event)
    {
        event.preventDefault()

        const URL = `http://localhost:3200/api/products/${id}`
        const XHR = new XMLHttpRequest()

        XHR.open("DELETE", URL)

        XHR.onreadystatechange = () =>
        {
            if (XHR.readyState === 4)
            {
                let { message } = JSON.parse(XHR.responseText)

                if (XHR.status === 200)
                    setRedirect("/")
                    
                setError(XHR.status === 200 ? null : message)
                setLoaded(true)
            }
        }

        XHR.setRequestHeader('Authorization', token)
        XHR.send()
    }

    function onRetract(event)
    {
        event.preventDefault()
        setRedirect(`/product/${id}`)
    }

    // Component did mount
    if (!loaded)
    {
        const URL = `http://localhost:3200/api/products/${id}`
        const XHR = new XMLHttpRequest()

        XHR.open("GET", URL)

        XHR.onreadystatechange = () =>
        {
            if (XHR.readyState === 4)
            {
                let product = JSON.parse(XHR.responseText).product

                if (XHR.status === 200)
                    setProduct(product)
                    
                setLoaded(true)
            }
        }

        XHR.send()
    }

    // Render
    const loadingStyle = { display: "flex", justifyContent: "center" }
    
    if (!token)
        return <E403/>

    if (!loaded)
        return [
            <h1 className="title" key={0}>Encore un peu de patience...</h1>,
            <div style={ loadingStyle } key={1}><Loading/></div>,
        ]

    if (!product)
        return [
            <h1 className="title" key={0}>Produit inexistant</h1>,
            <p className="error" key={1}>Ce produit n'existe pas ou a été retiré.</p>,
        ]

    if (redirect)
        return <Redirect to={ redirect }/>
    
    return [
        <Helmet key={0}>
            <title>Supprimer un produit - STUFF MANAGER</title>
        </Helmet>,
        <h1 className="title" key={1}>Supprimer { product.name } ?</h1>,
        error && <p className="error" key={2}>{ error }</p>,
        <p key={3}>Voulez-vous vraiment supprimer ce produit ?</p>,
        <div className="deleteForm" key={4}>
            <form action="" className="field" method="POST" onSubmit={ onConfirm }>
                <input className="link" type="submit" value="OUI"/>
            </form>
            <form action="" className="field" method="POST" onSubmit={ onRetract }>
                <input className="link" type="submit" value="NON"/>
            </form>
        </div>
    ]
}

DeleteProductForm.propTypes = {
    token: PropTypes.string
}

export default DeleteProductForm