import { Component } from 'react';
import { Route, Redirect, BrowserRouter as Router, Switch } from 'react-router-dom';
import { Helmet } from 'react-helmet'

import './App.css';

import Navbar from './Templates/Navbar';
import Footer from './Templates/Footer';

import E404 from './Views/E404';
import ProductsList from './Views/ProductsList';
import Product from './Views/Product';
import LoginForm from './Views/LoginForm';
import RegisterForm from './Views/RegisterForm';
import AddProductForm from './Views/AddProductForm';
import ModifyProductForm from './Views/ModifyProductForm';
import DeleteProductForm from './Views/DeleteProductForm';

class App extends Component
{
    state = {
        connected: sessionStorage.getItem('TOKEN')
            && sessionStorage.getItem('userID') ? true : false,
        token: sessionStorage.getItem('TOKEN'),
        userID: sessionStorage.getItem('userID')
    }

    logout = () =>
    {
        sessionStorage.clear()
        this.setState({ connected: false })
    }

    tryLogin = (newToken, userId) =>
    {
        sessionStorage.setItem('TOKEN', `Bearer ${newToken}`)
        sessionStorage.setItem('userID', userId)
        this.setState({ connected: true })
    }
    
    render()
    {
        const { connected, token } = this.state

        return (
            <div className="App">
                <Helmet>
                    <title>STUFF MANAGER</title>
                </Helmet>          
                <Router>
                    <Navbar admin={ connected } logoutHandler={ this.logout }/>
                    <Switch>
                        <Route exact path="/">
                            <ProductsList/>
                        </Route>
                        <Route exact path="/addProduct">
                            <AddProductForm token={ token }/>
                        </Route>
                        <Route exact path="/login">
                            <LoginForm setTokenHandle={ this.tryLogin }/>
                        </Route>
                        <Route exact path="/logout">
                            <Redirect to="/"/>
                        </Route>
                        <Route exact path="/register">
                            <RegisterForm/>
                        </Route>
                        <Route path="/product/:id/modify">
                            <ModifyProductForm token={ token }/>
                        </Route>                        
                        <Route path="/product/:id/delete">
                            <DeleteProductForm token={ token }/>
                        </Route>
                        <Route exact path="/product/:id">
                            <Product admin={ connected }/>
                        </Route>
                        <Route path="*">
                            <E404/>
                        </Route>
                    </Switch>
                    <Footer/>
                </Router>
            </div>
        )
    }
}

export default App
