import React, { Component } from 'react'
import { BrowserRouter as Router, useLocation } from 'react-router-dom';
import PropTypes from 'prop-types'

const MenuOption = ({ hRef, content, onclick }) =>
{
    const currentRoute = useLocation().pathname
    const isSelected = (currentRoute === hRef)

    return (
        <Router>
            <li className={ `menu__option ${ isSelected && "menu__option--selected" }` }>
            {
                isSelected ? (
                    <span>{content}</span>
                ) : (
                    <a href={ hRef } onClick={ onclick }>{ content }</a>
                )
            }
            </li>
        </Router>
    )
}

MenuOption.propTypes = {
    hRef: PropTypes.string.isRequired,
    content: PropTypes.string.isRequired,
    onclick: PropTypes.func
}

class Navbar extends Component
{
    static propTypes = {
        admin: PropTypes.bool.isRequired,
        logoutHandler: PropTypes.func.isRequired
    }

    render()
    {
        const { admin, logoutHandler } = this.props

        return (
            <nav className="navbar">
                <div className="logo"><a href="/">Stuff Manager</a></div>
                <ul className="menu">
                {
                    admin ? [
                        <MenuOption key={0} hRef="/" content="Liste des produits"/>,
                        <MenuOption key={1} hRef="/addProduct" content="Ajouter un produit"/>,
                        <MenuOption key={2} hRef="/logout" content="Déconnexion" onclick={ logoutHandler }/>
                    ] : [
                        <MenuOption key={0} hRef="/" content="Liste des produits"/>,
                        <MenuOption key={3} hRef="/login" content="Connexion"/>,
                        <MenuOption key={4} hRef="/register" content="Inscription"/>
                    ]
                }
                </ul>
            </nav>
        )
    }
}

export default Navbar